import React, { useState } from "react";
import './Nav.scss';
import NavItem from '../NavItem/NavItem.js';

const Nav = props => {
    return (
        <nav className={ (props.activeState) ? "nav active" : "nav" }>
            <ul>
                <NavItem path="/novostnik/" text="Главная" hideMenu={ props.hide } />
                <NavItem path="/novostnik/News" text="Новости" hideMenu={ props.hide } />
                <NavItem path="/novostnik/Contacts" text="Контакты" hideMenu={ props.hide } />
            </ul>
            <div className="hamburger-wrap"> 
                <div onClick={ () => props.changeState() } className="hamburger"> <span></span> </div>
            </div>
        </nav>
    );
}

export default Nav;