            `
                <p>
                    Издание Deadline сообщает, что очередной части франшизы удалось собрать кассу свыше 700 миллионов долларов.
                </p>
                <p>
                    “Форсаж 9” стал единственным фильмом, чьи общие кассовые сборы после начала пандемии преодолели эту отметку. Другим картинам повезло меньше: их кассовые сборы достигали не более 500 миллионов долларов.
                </p>
                <p>
                    Только в США прокат “Форсажа 9” принес фильму 172 миллиона долларов, в международном прокате сборы составили 528 миллионов долларов.
                </p>
                <p>
                    Общие сборы всей знаменитой франшизы “Форсаж” на данный момент превысили отметку в 6,5 миллионов долларов.
                </p>
            `
