            `
                <p>
                    Российская фигуристка Елизавета Нугуманова подтвердила в своём «инстаграме» информацию о том, что 19-летняя Мария Талалайкина будет выступать за сборную Италии.
                </p>
                <p>
                    Подписчики Елизаветы Нугумановой спросили фигуристку, как она относится к переходу Талалайкиной в сборную Италии.
                </p>
                <p class="news-page__quote">
                    «Я буду безумно скучать! Но Маша обещает приезжать. Мне будет очень её не хватать», – ответила Нугуманова в «сториз».
                </p>
                <p>
                    Мария Талалайкина — российская фигуристка, выступающая в одиночном катании. Мастер спорта России, бронзовый призёр Кубка России – 2020. Начинала работу под руководством тренера Ксении Дорониной. В последнее время выступала и тренировалась под управлением Романа Усатова и Евгения Рукавицына.
                </p>
            `               
