import React from "react";
import './NewsList.scss';
import NewsItem from '../NewsItem/NewsItem.js';
import newsData from '../../newsData/newsData.js';

const NewsList = props => {
    const filterResult = newsData && newsData.filter( (item, index) => {
        return ( 0 < props.newsCount ) ? index < props.newsCount : newsData;
    });
    
    return (
        <div className='news'>
            <ul className='news__list'>
                {                    
                    filterResult && filterResult.map( (item, index) => {
                        const date = new Date(item.publishedAt);

                        return (
                            <NewsItem
                                key = { index }
                                newsName = { item.title }
                                sourceName = { item.source }                                
                                publishedDay = { date.getDate() }
                                publishedMonth = { date.getMonth() + 1 }
                                content = { item.content }
                                img = { item.urlToImage }
                            />
                        )
                    })
                }   
            </ul>
        </div>
    );
}

export default NewsList;