import React from "react";
import {withRouter} from 'react-router-dom';

const NewsItem = props => {
    const filterNewsName = props.newsName.replace(/([—,”,“,«,»,:])/g, '')
                                .replace(/(\s)/g, '-')
                                .toLowerCase();

    return (
        <li onClick={ () => {
                props.history.push(
                    '/novostnik/news/' + filterNewsName,
                    [{
                        title : props.newsName,
                        sourceName : props.sourceName,
                        publishedDay : props.publishedDay,
                        publishedMonth : props.publishedMonth,
                        content : props.content,
                        img : props.img
                    }]
                )

                document.body.scrollTop = document.documentElement.scrollTop = 0;
            }}
            className="news__item"
        >
            <h2 className="news__title"> { props.newsName } </h2>
            <div className="news__inner-wrapper">
                <p className="news__author"> { props.sourceName } </p>
                <div className="news__published">
                    <span> { props.publishedDay } </span>
                    <span> / </span>
                    <span> { props.publishedMonth } </span>
                </div>
            </div>
        </li>
    );
}

export default withRouter(NewsItem);