import React from "react";
import './Contacts.scss';
import meImg from '../../img/me.webp';

const Contacts = () => (
    <div className="contacts__wrapper">
        <div className="contacts__leftside">
            <h1 className="contacts__title"> Контакты </h1>
            <a href="tel:+79991234576" className="contacts__phone"> +7 (999) 123 45 76 </a>
            <p className="contacts__made-by"> Сергей Тормозов </p>
            <a href="mailto:mail@domain.com" className="contacts__email"> mail@domain.com </a>
            <div className="contacts__skills-wrapper">
                <p> Веб-дизайнер и JavaScript разработчик </p>
                <div> ES5, ES6, <span className="accent">React</span>, </div>
                <div> HTML, CSS, <span className="accent"> SCSS </span> </div>
                <div> Adobe Photoshop, Figma </div>
            </div>
        </div>
        <img src={ meImg } alt="Это я тут, да" />
    </div>
);

export default Contacts;