import React from "react";
import './NewsDetails.scss';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';

const NewsDetails = props => {
    const details = props.location.state[0];
    const title = details.title;

    let wordArray = title.split(/\s+/);
    let lastWord = wordArray.pop();
    let firstPart = wordArray.join(' ');

    const htmlContent = details.content;

    return (
        <div className="news-page">
            <div className="news-page__left-side">
                <h1 className="news-page__title page-title"> { firstPart } <span> { lastWord } </span> </h1>
                <p className="news-page__author"> { details.sourceName } </p>                   
                <div className="news-page__published">
                    <span> { details.publishedDay } </span>
                    <span> / </span>
                    <span> { details.publishedMonth } </span>
                </div>
            </div>
            <div className="news-page__right-side">
                <img src={ details.img } alt={ firstPart } className="news-page__main-img" />
                <div className="news-page__content">
                    { ReactHtmlParser(htmlContent) }
                </div>
            </div>
        </div>
    );
}

export default NewsDetails;